/**
 * 生成 uuid
 * @returns uuid
 */
export function uuid(): string {
  /* jshint ignore:start */
  let d = new Date().getTime();
  const uuidValue = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
    const r = (d + Math.random() * 16) % 16 | 0;
    d = Math.floor(d / 16);
    return (c === 'x' ? r : (r & 0x7) | 0x8).toString(16);
  });
  return uuidValue;
  /* jshint ignore:end */
};

/**
 * 日期格式化
 * @param d - date
 * @returns
 */
export function getDateFormatString(d: Date): string {
  return d.getFullYear() + '-' + ('0' + (d.getMonth() + 1)).slice(-2) + '-' + ('0' + d.getDate()).slice(-2);
}
/**
 * 统一路径格式
 * @param path 
 * @returns 
 */
export function formatApiPath(path: string): string {
  if(path.slice(-1) === '/') path = path.slice(0, -1);
  if(path.slice(0, 1) === '/') path = path.slice(1);
  return path;
}