/**
 * 实现基于AWS的OSS存取
 * 参考 https://docs.aws.amazon.com/sdk-for-javascript/v3/developer-guide/s3-example-photo-album.html 实现
 */
import { S3Client, PutObjectCommand } from '@aws-sdk/client-s3';
import { uuid, getDateFormatString, formatApiPath } from './utils';
import type { Config, Creator, Credentials, UploadParams, UploadResult } from './index';

const PRESET_CONTENT_TYPES = ['image/gif', 'image/jpeg', 'image/png']; // 图片文件类型
const MAX_TRY_TIMES = 2; // 失败后尝试请求次数

export function create(config: Required<Config>): Creator {
  console.error('yzb-aws');
  let credentials: Credentials;
  const auth = config.auth;

  let shareS3Client: S3Client | null = null;
  let shareS3ClientPromise: Promise<S3Client> | null = null;
  /**
   * 创建 s3Client，支持同时调用多次，只会产生同一个 shareS3Client
   */
  function createS3Client(): Promise<S3Client> {
    if (!shareS3ClientPromise) {
      shareS3ClientPromise = new Promise((resolve, reject) => {
        auth().then((data) => {
          credentials = data;
          const { id: accessKeyId, key: secretAccessKey, token: sessionToken, region } = data;
          const s3Client = new S3Client({
            region,
            credentials: {
              accessKeyId,
              secretAccessKey,
              sessionToken,
            },
          });
          resolve(s3Client);
        })
        .catch((err) => {
          console.log('err', err);
          reject(err);
        });
      });
    }
    return shareS3ClientPromise;
  }
  /**
   * 重置 shareS3Client 引用
   */
  function resetS3Client(): void {
    shareS3Client = null;
    shareS3ClientPromise = null;
  }

  /**
   * AWS上传
   * @param params - 上传参数
   * @param tryTimes - 上传尝试次数
   * @returns 成功返回上传对象，失败返回 null
   */
  async function upload(params: UploadParams, tryTimes?: number): Promise<UploadResult> {
    const paramsWithDefault = {
      pathPrefix: '',
      datePrefix: true,
      rename: true,
      ...params,
    };
    if (!shareS3Client) {
      shareS3Client = await createS3Client();
    }
    const {bucketName, bucketContentPath, cdnUrl} = credentials;
    // 处理上传文件名
    let fileKey = encodeURIComponent(paramsWithDefault.file.name);
    if (paramsWithDefault.rename) {
      const extName = fileKey.lastIndexOf('.') !== -1 ? fileKey.split('.').pop() : '';
      fileKey = uuid() + (extName ? `.${extName}` : '');
    }
    if (paramsWithDefault.datePrefix) {
      const now = new Date();
      fileKey = getDateFormatString(now) + '/' + fileKey;
    }
    if (paramsWithDefault.pathPrefix) {
      fileKey = formatApiPath(paramsWithDefault.pathPrefix) + '/' + fileKey;
    }
    fileKey = formatApiPath(bucketContentPath) + '/' + fileKey;

    // 组织上传参数
    const uploadParams = {
      Bucket: bucketName,
      Key: fileKey,
      Body: params.file,
    };

    // 图片文件，设置上传 contentType，防止直接打开上传后的文件触发自动下载
    const fileType = paramsWithDefault.file.type;
    if (PRESET_CONTENT_TYPES.includes(fileType)) {
      Object.assign(uploadParams, {
        ContentType: fileType,
      });
    }

    let result = null;
    try {
      result = await shareS3Client.send(new PutObjectCommand(uploadParams));
    } catch (err) {
      console.error('There was an error uploading your file: ', err);
      if (typeof tryTimes === 'undefined' || tryTimes < MAX_TRY_TIMES) {
        resetS3Client();
        result = await upload(params, typeof tryTimes === 'number' ? tryTimes + 1 : 2);
      }
    }
    if (result) {
      return {
        url: formatApiPath(cdnUrl) + '/' + fileKey
      };
    }
    return null;
  }
  return {
    upload
  };
}
